.header {
  width: 100%;
  z-index: 9;
  position: relative;
  padding: 20px 0 80px 0;
}
.header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: linear-gradient(180deg, #1A4A8C -3.27%, rgba(0, 189, 214, 0.33) 100%);
}
.header-logo {
  transition: 0.3s opacity ease;
  position: relative;
  z-index: 9;
}
.header .ctn {
  position: relative;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header nav ul {
  list-style: none;
  display: flex;
  gap: 30px;
}
.header nav a {
  color: #fff;
  font-size: 1.6rem;
  font-weight: 700;
  position: relative;
}
.header nav a::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 0;
  background-color: #fff;
  height: 2px;
  transition: 0.3s width ease;
}
.header nav a:hover::after, .header nav a.active::after {
  width: 100%;
  right: unset;
  left: 0;
}
.header-hamburger {
  display: none;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  width: 30px;
  height: 20px;
  position: relative;
  z-index: 9;
}
.header-hamburger div {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: 0.5s all ease;
}
.header-hamburger div:nth-child(2) {
  top: calc(50% - 1px);
}
.header-hamburger div:last-child {
  bottom: 0;
}
.header-hamburger.enable div {
  background-color: #1A4A8C;
}
.header-hamburger.enable div:first-child {
  transform: rotate(45deg);
  top: calc(50% - 1px);
}
.header-hamburger.enable div:nth-child(2) {
  width: 0;
}
.header-hamburger.enable div:last-child {
  top: calc(50% - 1px);
  bottom: unset;
  transform: rotate(-45deg);
}
.header .ctm-block {
  position: relative;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: calc(50% - 100px);
  width: 90%;
  max-width: 510px;
  text-align: center;
  margin: auto;
  display: none;
}

body.enable .header {
  height: 500px;
}
body.enable .header .ctm-block {
  display: flex;
}

@media (max-width: 992px) {
  .header {
    padding: 20px 0;
    min-height: 72px;
  }
  .header nav {
    display: none;
  }
  .header-hamburger {
    display: flex;
  }
  .header.scroll .ctn {
    z-index: 999;
  }
  .header.scroll .ctn::before {
    opacity: 1;
  }
  .header .ctn {
    position: fixed;
    left: 5%;
  }
  .header .ctn::before {
    position: absolute;
    content: "";
    left: -25%;
    top: -50px;
    width: 150%;
    height: 100px;
    background: linear-gradient(to bottom, #000 0%, rgba(0, 0, 0, 0.8) 30%, rgba(0, 0, 0, 0.56) 55%, rgba(0, 0, 0, 0.16) 85%, rgba(0, 0, 0, 0) 100%);
    transition: 0.3s opacity ease;
    opacity: 0;
  }
  body.enable .header .ctn {
    justify-content: center;
    position: relative;
    left: unset;
  }
  body.enable .header .ctn::before {
    width: 100%;
    opacity: 0 !important;
  }
  body.enable .header .header-hamburger {
    display: none;
  }
}
