.filters {
  position: relative;
  z-index: 9;
}
.filters-block {
  width: 100%;
  position: relative;
  background-color: #1A4A8C;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 40px 20px;
  border-radius: 10px;
  top: -180px;
  margin-bottom: -100px;
  gap: 30px;
  filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.2)) drop-shadow(0px 11px 20px rgba(0, 0, 0, 0.3));
}

.input-custom input[type=text] {
  width: 100%;
  outline: inherit;
  border: none;
  background-color: transparent;
  background-color: #fff;
  border-radius: 5px;
  border: 1.5px solid #fff;
  padding: 22.5px 10px 7.5px 35px;
  color: #1A4A8C;
  font-size: 1.4rem;
  transition: 0.3s all ease;
  position: relative;
}
.input-custom input[type=text]::placeholder {
  color: #486EA3;
  opacity: 0.4;
  margin-top: 100px;
}
.input-custom input[type=text]:focus {
  border-color: #1A4A8C;
}
.input-custom input[type=radio] {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 100px;
  background-color: transparent;
  border: 2px solid #fff;
  transition: 0.3s all ease;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input-custom input[type=radio]:checked {
  background-color: #fff;
}
.input-custom input[type=radio]:checked::before {
  opacity: 1;
}
.input-custom input[type=radio]::before {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 100px;
  background-color: #559FC7;
  opacity: 0;
}
.input-custom input[type=checkbox] {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  border: 2px solid #fff;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s background-color ease;
}
.input-custom input[type=checkbox]:checked {
  background-color: #fff;
}
.input-custom input[type=checkbox]:checked::before {
  opacity: 1;
}
.input-custom input[type=checkbox]::before {
  position: absolute;
  content: "";
  background-image: url("../assets/icons/icon-check.svg");
  width: 12px;
  height: 10px;
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 0.3s opacity ease;
}
.input-custom__checkbox, .input-custom__radio, .input-custom__submit {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.input-custom__submit {
  width: 100%;
  justify-content: center;
}
.input-custom__checkbox {
  padding-right: 20px;
}
.input-custom__checkbox label {
  color: #fff;
  font-size: 1.6rem;
}
.input-custom__radio {
  position: relative;
  padding-right: 20px;
}
.input-custom__radio label {
  color: #fff;
  font-size: 1.6rem;
}
.input-custom__block {
  position: relative;
}
.input-custom__block .error-message {
  color: #E92222;
  font-size: 1.2rem;
  padding: 5px 0 0 5px;
  display: none;
  position: absolute;
  bottom: -20px;
}
.input-custom__ctn {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.input-custom__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input-custom__item img {
  position: absolute;
  left: 8px;
}
.input-custom__item label {
  position: absolute;
  font-size: 1.2rem;
  top: 5px;
  left: 35px;
  color: #1A4A8C;
  z-index: 9;
}
.input-custom__item::before {
  position: absolute;
  content: "";
  background-image: url("../assets/icons/icon-danger.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 30px;
  height: 20px;
  z-index: 9;
  right: 0;
  opacity: 0;
}
.input-custom__options {
  display: flex;
  gap: 5px;
}
.input-custom__block.error input {
  border-color: #E92222;
}
.input-custom__block.error label {
  color: #E92222;
}
.input-custom__block.error .error-message {
  display: block;
}
.input-custom__block.error .input-custom__item::before {
  opacity: 1;
}

.btn {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 700;
  background-color: #00BDD6;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  min-width: 150px;
  width: fit-content;
  padding: 12.5px 20px;
  cursor: pointer;
  transition: 0.3s background-color ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn:hover {
  background-color: #559FC7;
}

.aos-custom {
  opacity: 0;
}
.aos-custom-fade-up {
  opacity: 0;
  transform: translateY(40px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-fade-left {
  transform: translateX(-100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-fade-right {
  transform: translateX(100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-scale {
  transform: scaleX(0);
  transition: 1s ease transform;
}
.aos-custom-scaleX, .aos-custom-scaleXreverse {
  transform: scale(0);
  position: relative;
  transition: 1s ease all;
}
.aos-custom-scaleX {
  left: -150px;
}
.aos-custom-scaleXreverse {
  right: -150px;
}
.aos-custom-rotate {
  transform: scale(0.5);
  opacity: 0;
  transition: 1s ease all;
}
.aos-custom.aos-animate {
  opacity: 1;
}

.aos-activate.aos-animate .aos-custom-scale {
  transform: scale(100%);
}
.aos-activate.aos-animate .aos-custom-scaleX {
  transform: scale(1);
  left: 0;
}
.aos-activate.aos-animate .aos-custom-scaleXreverse {
  transform: scale(1);
  right: 0;
}
.aos-activate.aos-animate .aos-custom-fade-left,
.aos-activate.aos-animate .aos-custom-fade-right {
  transform: translateX(0);
}
.aos-activate.aos-animate .aos-custom-fade-up {
  transform: translateY(0);
}
.aos-activate.aos-animate .aos-custom-rotate {
  transform: scale(1);
  opacity: 1;
}
.aos-activate.aos-animate .aos-custom-fade-up,
.aos-activate.aos-animate .aos-custom-fade-left,
.aos-activate.aos-animate .aos-custom-fade-right {
  opacity: 1;
}

.home-welcome h1, .home-welcome p {
  opacity: 0;
  transform: translateY(100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}

.filters-block {
  opacity: 0;
  transition-delay: 0.5s;
}

.filters-block.aos-animate {
  opacity: 1;
}

.home-welcome.aos-animate h1, .home-welcome.aos-animate p {
  opacity: 1;
  transform: translateY(0);
}
.home-welcome.aos-animate p {
  transition-delay: 0.25s;
}